	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"Researcher of Natural Language Processing, Information Retrieval, and Search/Recommendation Systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/ICASSP2019-music/index.md absPath of file >>> MarkdownRemark","html":"<p><b>This paper is published in the in Proceedings of The 44th IEEE International Conference on Acoustics, Speech, and Signal Processing (ICASSP 2019), Brighton, U.K., May 12-17, 2019. IEEE.</b></p>\n<p>Full paper:\n<a href=\"/ICASSP_2019___Music_Generation-81c090fb9606994fe2125873e57dbc71.pdf\" target=\"_blank\">Here</a>,\n<a href=\"https://arxiv.org/abs/1811.00162\" target=\"_blank\">arXiv</a></p>\n<p>Automatic melody generation has been a long-time aspiration for both AI researchers and musicians.\nHowever, learning to generate euphonious melodies has turned out to be highly challenging.\nThis paper introduces 1) a new variant of variational autoencoder (VAE), where the model structure is designed in a modularized manner in order to model polyphonic and dynamic music with domain knowledge, and 2) a hierarchical encoding/decoding strategy, which explicitly models the dependency between melodic features. The proposed framework is capable of generating distinct melodies that sounds natural, and the experiments for evaluating generated music clips show that the proposed model outperforms the baselines in human evaluation. (The first three authors contributed equally.)</p>","fields":{"tagSlugs":["/tags/music-information-retrieval/","/tags/generation/","/tags/vae/"]},"frontmatter":{"title":"Modeling Melodic Feature Dependency with Modularized Variational Auto-Encoder","tags":["Music Information Retrieval","Generation","VAE"],"date":"2019-02-7T22:40:32.169Z","description":"Yu-An Wang*, Yu-Kai Huang*, Tzu-Chuan Lin*, Shang-Yu Su, and Yun-Nung Chen","conference":"ICASSP2019"}}},"pathContext":{"slug":"/publications/icassp2019-music/"}}

